"use client";

import { cn } from "@/lib/utils";
import React, { useEffect, useRef, useState } from "react";

interface NumberPickerProps {
  min?: number;
  max?: number;
  value: number | undefined;
  onChange: (value: number | undefined) => void;
}

export function NumberPicker({
  min = 1,
  max = 30,
  value,
  onChange,
}: NumberPickerProps) {
  const numbers = Array.from({ length: max - min + 1 }, (_, i) => i + min);
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  const [internalValue, setInternalValue] = useState(value);
  
  // Set initial scroll position
  useEffect(() => {
    const container = scrollContainerRef.current;
    if (container && value !== undefined) {
      const selectedElement = container.querySelector(`[data-value="${value}"]`) as HTMLElement;
      if (selectedElement) {
        const containerWidth = container.offsetWidth;
        const elementWidth = selectedElement.offsetWidth;
        const scrollLeft = selectedElement.offsetLeft - (containerWidth / 2) + (elementWidth / 2);
        container.scrollLeft = scrollLeft;
      }
    }
  }, [value]);

  const handleScroll = () => {
    const container = scrollContainerRef.current;
    if (!container) return;

    const scrollLeft = container.scrollLeft;
    const containerWidth = container.offsetWidth;
    const center = scrollLeft + containerWidth / 2;

    let closestElement: HTMLElement | null = null;
    let minDistance = Infinity;

    container.querySelectorAll<HTMLElement>('[data-value]').forEach((element) => {
      const elementWidth = element.offsetWidth;
      const elementCenter = element.offsetLeft + elementWidth / 2;
      const distance = Math.abs(center - elementCenter);

      if (distance < minDistance) {
        minDistance = distance;
        closestElement = element;
      }
    });

    if (closestElement) {
      const newValue = parseInt(closestElement.dataset.value || "0", 10);
      if (newValue !== internalValue) {
        setInternalValue(newValue);
      }
    }
  };

  const handleScrollEnd = () => {
     if (internalValue !== undefined) {
        onChange(internalValue);
     }
  }

  return (
    <div className="relative w-full h-16 flex items-center justify-center overflow-hidden">
      <div 
        ref={scrollContainerRef}
        onScroll={handleScroll}
        onMouseUp={handleScrollEnd}
        onTouchEnd={handleScrollEnd}
        className="flex items-center gap-4 overflow-x-auto snap-x snap-mandatory no-scrollbar px-[calc(50%-2rem)]"
      >
        {numbers.map((num) => (
          <div
            key={num}
            data-value={num}
            className={cn(
                "snap-center shrink-0 w-16 h-16 flex items-center justify-center text-4xl font-bold transition-all duration-150 ease-in-out",
                internalValue === num ? "text-primary scale-100" : "text-muted-foreground/30 scale-75"
            )}
          >
            {num}
          </div>
        ))}
      </div>
      <div className="absolute inset-y-0 left-1/2 -translate-x-1/2 w-0.5 bg-border" aria-hidden="true" />
      <div className="absolute inset-y-0 left-1/2 -translate-x-1/2 w-20 h-full border-y-2 border-dashed border-transparent" aria-hidden="true" />
    </div>
  );
}